@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@500;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body * {
  font-family: 'Manrope', sans-serif;
}

div.nav-bar {
  background-color: #FDD835;
  color: #757575;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

div.nav-bar img {
  height: 20px;
  margin: 20px;
}

.nav-bar ul {
  display: flex;
}

ul > li {
  display: block;
  text-decoration: none;
  list-style: none;
  font-weight: bold;
  margin-right: 15px;
}

ul > li:last-child {
  margin-right: 0;
}

button {
  height: 40px;
  width: 200px;
  border-radius: 4px;
  border: solid 1px white;
  margin: 10px 15px;
}

button > span::before {
  content: "\f007";
}
* {
    margin: 0;
    padding: 0;
    font-family: 'Manrope', sans-serif;
    color: black;
}

.nav-bar > nav {
    width: 50%;
}

.nav-bar > nav > ul {
    display: flex;
    justify-content: space-around;
}

.nav-bar > nav > ul > li.active {
    background-color: #feeb9a;
}

/* Wrapper */
.wrapper {
    max-width: 1200px;
}

section.section > * {
    margin: 20px 70px;
}

.section-title {
    margin: 50px 0px 20px;
    border-bottom: 2px solid #000;
} 

.section-nav > ul {
    display: flex;
    justify-content: space-around;
    padding: 30px;
}

.section-nav li {
    color: #757575;
}

.section-nav li:hover, .section-nav li.active {
    color: #000;
    font-weight: bold;
}

main.section {
    width: 75%;
}
main.section > .section-nav {
    display: flex;
    justify-content: space-between;
}

main.container {
    margin: 0 70px;
    display: flex;
    justify-content: space-between;
}

.aside-ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.aside-li {
    color: #757575;
}

.aside button {
    margin: 0;
    background-color: #fff;
    color: #757575;
    text-align: left;
    border: none;
}

.aside-ul > .aside-li > .aside-li-text:focus {
    border:none;
}

.aside button:hover {
    color: #000000;
    border: none;
    font-weight: bold;
}

section ul.nav-profile {
    padding: 8px 0;
    list-style-type: none;
    display: flex;
    justify-content: space-around;
}

.nav-profile-item {
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.5px;
    padding: 0;
}

.nav-profile-item:hover {
    font-weight: 700;
}

div.statistics {
    padding-left: 30px;
    display: flex;
    flex-direction: column;
}

canvas {
    border: solid 1px #FDD835;
}

.graphic-card {
    margin: 15px;
    width: 700px;
    border-radius: 5px;
}

.graphic-title {
    padding: 15px;
    margin: 0;
    border-radius: 5px 5px 0 0;
    border-bottom: solid 1px #757575;
}

.graphic-diagram {
    padding: 15px;
    border-radius: 0 0 5px 5px;
}

